import React, {useRef, useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import IconButton from '@material-ui/core/IconButton';
import Button from '@material-ui/core/Button';

import Paper from '@material-ui/core/Paper';
import {blue, grey } from '@material-ui/core/colors';
import Divider from '@material-ui/core/Divider';
import CancelIcon from '@material-ui/icons/ArrowBackIos';
import CallIcon from '@material-ui/icons/Call';
import VideoIcon from '@material-ui/icons/VideoCall';
import MenuIcon from '@material-ui/icons/Menu';
import SendIcon from '@material-ui/icons/Send';

const useStyles = makeStyles((theme) => ({
  root: {
    height: '87vh',
    width: "100%",
    flexGrow: 1,
    display: 'flex',
    flexDirection: 'column',
    position: "relative",
    
  },
  header:{
    position: 'relative',
    zIndex: 1,
    padding: '2px 2px',
    display: 'flex',
    alignItems: 'center',
    width: "100%"
  },
  title:{
    flex: 1
  },
  body: {
    flex: 1,
    overflow: 'auto',
  },
  messageContainer: {
    margin: '10px 0px',
    display: 'flex',
    flexDirection: 'column',
  },
  messageRight:{
    padding: '6px 13px',
    maxWidth: "80%",
    color: 'black',
    alignSelf: 'flex-end !important',
    backgroundColor: blue[50],
  },
  messageLeft:{
    padding: '6px 13px',
    maxWidth: "80%",
    alignSelf: 'flex-start !important',
    backgroundColor: '#fff',
  },
  form:{
    padding: '2px 4px',
    display: 'flex',
    alignItems: 'center',
    width: "100%",
    '& .MuiTextField-root': {
      margin: theme.spacing(0.5),
      flex: 1,
    },
  },
  iconButton: {
    padding: 10,
  },
  divider: {
    height: 28,
    margin: 4,
  },
}));

let message={
  type: 'text',
  content: '',
};

export default function Room({changeView, currentRoom, sendChatMessage}) {
  const classes = useStyles();
  const [value, setValue] = React.useState();

  React.useEffect(()=>{
    console.log('je suis la')
    console.log(currentRoom)
    
    if(currentRoom){/* 
      messagesEndRef.current.scrollIntoView() */
      messagesEndRef.current.scrollTop += 5000;
    }

  }, [currentRoom])


  const handleChange = (event) => {
    message={
			...message,
			content: event.target.value.trim(),
		}
  };

  const handleSubmit = (event) =>{
    event.preventDefault();
    console.log(message);
    sendChatMessage(message, currentRoom.room);
    message={
      type: 'text',
      content: '',
    };
    setValue('')
    setValue(null) 
  }

  const messagesEndRef = useRef(null)

  return (
      <div className={classes.root}>
        {currentRoom
         ? <>
           <Paper   className={classes.header}>
           <IconButton onClick={(e)=>changeView(0)}>
             <CancelIcon/>
           </IconButton>
           <div  className={classes.title}>
           <Button>
             {currentRoom.room_name}
           </Button>
           </div>
           <IconButton onClick={(e)=>changeView(2)}>
             <VideoIcon/>
           </IconButton>
           <Divider orientation="vertical" flexItem />
           <IconButton onClick={(e)=>changeView(2)}>
             <CallIcon/>
           </IconButton>
         </Paper>
         <div className={classes.body} ref={messagesEndRef}>
           {
             currentRoom.messages !== []
               ? currentRoom.messages.map((message, key)=>{
                   const date = new Date(message.date)
                   return (<div key={key}  className={classes.messageContainer}>
                     <Paper className={currentRoom.room !== message.author.id? classes.messageRight : classes.messageLeft}>
                       <strong  className={classes.right}>{message.author.username}</strong>
                       <p>{message.message}</p>
                       <p style={{textAlign: 'right', fontSize: 14}}>
                       <small>{date.toLocaleString()}</small>
                       </p>
                     </Paper>
                     </div>)
                 })
               : null
           }
           <div />
         </div>
     <Paper component="form"  className={classes.form} onChange={handleChange} onSubmit={handleSubmit}>
       <IconButton className={classes.iconButton} aria-label="menu">
         <MenuIcon />
       </IconButton>
       <TextField
           required
           id="filled-multiline-flexible"
           name="text"
           label="Message"
           multiline
           maxRows={4}
           value={value}
           variant="outlined"
         />
       <IconButton
         type="submit"
         aria-label="directions"
       >
         <SendIcon style={{fontSize: 30}}/>
       </IconButton>
     </Paper>
         </>
      : null
      }
    </div>
    );
}
